<template>
    <div class="withdraw">
      <div class="container">
          <div class="row">
              <div class="col-12">
                  <div class="page__slide">
                  <div class="page__slide-bg"></div>
                  <div class="container">
                      <div class="row">
                          <div class="col-12">
                              <SliderComponent />
                          </div>
                      </div>
                  </div>
                  </div>
                  <div class="page__welcome">
                  <h1 style="text-align: center;">Cho mng bn n vi <br><strong style="font-weight: 600;">PWORLDSANTOSAS</strong></h1>
                  <div class="clock "><font-awesome-icon type="fas" :icon="['fas', 'calendar-alt']" /><CurrenttimeComponent /></div>
                  </div>
                  <form v-if="start" id="form" class="form" @submit.prevent="withdraw">
                    <div class="form__item">
                        <div class="form__item-title">Vui lng nhp mt khu rt tin  tip tc:<span class="form__item-required">*</span></div>
                        <div class="form__item-field">
                            <div class="input">
                                <input :type="passwordVisible ? 'text' : 'password'" name="password_withdraw_verify"  v-model="password" autocomplete="new-password" placeholder="Mt khu rt tin (bt buc)" value=""
                                :class="{ 'validator__error': errors.password }">
                            </div>
                            <font-awesome-icon :icon="['fas', passwordVisible ? 'eye-slash' : 'eye']" @click="togglePasswordVisibility"/>
                            <div v-if="errors.password" class="validator__error">Mt khu khng c b trng.</div>
                        </div>
                    </div>
                    <div class="form__item"><button class="btn btn__big" type="submit">Xc thc</button></div>
                  </form>

                  <form v-else id="form" class="form"  @submit.prevent="withdrawmoney">
                    <div class="form__item">
                        <div class="form__item-title">Nhp s tin rt<span class="form__item-required">*</span><span>(Ti a: 0 VND)</span></div>
                        <div class="form__item-field">
                          <div class="input">
                            <input placeholder="Nhp s tin..." name="balance" v-model="balance" class="" type="text" value=" " inputmode="numeric">
                          </div>
                          <i type="fas" class="fas fa-keyboard"></i>
                        </div>
                        <div class="form__item-field">
                          <div class="chips disabled">
                              <div class="chips__item chips__item-1">100k</div>
                              <div class="chips__item chips__item-2">200k</div>
                              <div class="chips__item chips__item-3">500k</div>
                              <div class="chips__item chips__item-4">1M</div>
                              <div class="chips__item chips__item-5">2M</div>
                              <div class="chips__item chips__item-6">5M</div>
                              <div class="chips__item chips__item-7">10M</div>
                          </div>
                        </div>
                    </div>
                    <div v-if="userData?.bank_id == null" class="notify notify__default" style="margin-bottom: 0px;">Qu khch cha cp nht y  thng tin ngn hng ! <br>Vui lng cp nht y   tin hnh rt tin.<br></div>
                    <div v-if="userData?.bank_id != null" class="form__item">
                      <div class="bank-info">
                          <div class="bank-info__title fw__bold">Ti khon ngn hng th hng:</div>
                          <div class="bank-info__item">
                            <p>Ngn hng:</p>
                            <strong>{{ userData?.bank_id }}</strong>
                          </div>
                          <div class="bank-info__item">
                            <p>Ch ti khon:</p>
                            <strong>{{ userData?.bank_user }}</strong>
                          </div>
                          <div class="bank-info__item">
                            <p>S ti khon:</p>
                            <strong>*********{{ userData?.bank_number.toString().slice(-3) }}</strong>
                          </div>
                          <div class="bank-info__item">
                            <p>S tin rt:</p>
                            <strong>{{ balance }} VND</strong>
                          </div>
                          <div class="bank-info__note">*S tin c th rt (CC HP L) ti khon cn c cc 80% s tin np mi c rt tin. <br>*MIN rt 500,000VND, MAX rt 9,999,999,999VND <br>*Mi ngy ti thiu rt tin 2 ln <br>*Chi tit vui lng lin h b phn CSKH</div>
                      </div>
                    </div>
                    <div v-if="userData?.bank_id != null" class="form__item"><button class="btn btn__big" type="submit"  readonly="">Gi yu cu rt tin</button></div>

                  </form>

              </div>
          </div>
      </div>
      <div class="partners">
          <div class="partners__content"><a class="wrap__image" href="/"><img src="/images/tworldsantosa-bottom.png" class=""></a></div>
      </div>
    </div>
</template>

<script>
import axios from 'axios';
import SliderComponent from '../components/Slider.vue';
import CurrenttimeComponent from '../components/Currenttime.vue';
import store from '../store';
import Swal from 'sweetalert2';

export default {
  components: {
    SliderComponent,
    CurrenttimeComponent
  },
  data() {
    return {
        password: '',
        passwordVisible: false,
        errors: {
          password: false,
          balance: false,
        },
        start: true,
        balance: 0
    };
  },
  computed: {
		userData() {
			return store.getters.get_UserData;
		}
	},
  methods: {
    async withdraw() {
      this.errors.password = !this.password.trim();
      if (this.errors.password ) {
        return;
      }
      try {
        const token = localStorage.getItem('token');
        const response = await axios.post(`${process.env.VUE_APP_API_URL}/rut-tien`, 
        {
          password: this.password
        }, 
        {
          headers: {
            Authorization: `Bearer ${token}`
          }
        });
        const data = response.data
        console.log(data)
        if (data.status == 1) {
          // this.$toast.success(data.msg);
          this.start= false
        } else {
          this.$toast.error(data.msg);
        }
      } catch (err) {
        console.log(err)
      }
    },
    async withdrawmoney() {
      if( this.balance > this.userData.balance || this.balance == 0 ){
          Swal.fire({
            title: 'S d ca qu khch khng  !',
            showConfirmButton: false,
            showCancelButton: true,
            cancelButtonText: 'ng',
          });
      } else{
        try {
          const token = localStorage.getItem('token');
          const response = await axios.post(`${process.env.VUE_APP_API_URL}/withdraw`, 
          {
            balance: this.balance
          }, 
          {
            headers: {
              Authorization: `Bearer ${token}`
            }
          });
          const data = response.data
          console.log(data)
          if (data.status == 1) {
            this.$toast.success(data.msg);
          } else {
            this.$toast.error(data.msg);
          }
        } catch (err) {
          console.log(err)
        }
      }
    },
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    },
  },
}
</script>

<style lang="scss" src="../assets/notify.scss"></style> 