<template>
   <div class="bank">
   <div class="container">
      <div class="row">
         <div class="col-12">
            <div class="page__slide">
               <div class="page__slide-bg"></div>
               <div class="container">
                  <div class="row">
                     <div class="col-12">
                        <SliderComponent />
                     </div>
                  </div>
               </div>
            </div>
            <div class="page__welcome">
                <h1 style="text-align: center;">Cho mng bn n vi <br><strong style="font-weight: 600;">PWORLDSANTOSAS</strong></h1>
                <div class="clock "><font-awesome-icon type="fas" :icon="['fas', 'calendar-alt']" /><CurrenttimeComponent /></div>
            </div>
            <form v-if="userData?.bank_id == null" id="form" class="form"  @submit.prevent="addnewbank">
               <div class="form__item">
                  <div class="form__item-title">Chn ngn hng<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                        <select  autocomplete="new-password" name="bank_name" v-model="bank_name" placeholder="Chn ngn hng" value="">
                            <option :selected="true">Chn ngn hng</option>
                            <option v-for="(item, index) in banks" :key="index" >
                                {{ item.value }}
                            </option>
                        </select>
                    </div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">Ch ti khon<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input"><input format="ALPHABET" max="100" autocomplete="new-password" name="bank_user" v-model="bank_user" placeholder="T 3 ti 100 k t" value=""><span>0/100</span></div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">S ti khon<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input"><input format="Alphadash" max="100" nospace="true" autocomplete="new-password" name="bank_number" v-model="bank_number" placeholder="T 3 ti 100 k t" value=""><span>0/100</span></div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">CCCD Mt trc<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                      <input type="file" name="cccd_mt" @change="handleFileUpload">
                    </div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">CCCD Mt sau<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                      <input type="file" name="cccd_ms" @change="handleFileUploadms">
                    </div>
                  </div>
               </div>
               <div class="form__item"><button class="btn btn__big" type="submit" >Cp nht thng tin ngn hng</button></div>
            </form>
            <form v-else id="form" class="form"  @submit.prevent="addnewbank">
               <div class="form__item">
                  <div class="form__item-title">Chn ngn hng<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                        <select  autocomplete="new-password" name="bank_name"  placeholder="Chn ngn hng" disabled="">
                            <option selected>
                                {{ userData?.bank_id }}
                            </option>
                        </select>
                    </div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">Ch ti khon<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                      <input format="ALPHABET" max="100" autocomplete="new-password" name="bank_user" placeholder="T 3 ti 100 k t" :value="userData?.bank_user" disabled=""><span>0/100</span>
                    </div>
                  </div>
               </div>
               <div class="form__item">
                  <div class="form__item-title">S ti khon<span class="form__item-required">*</span></div>
                  <div class="form__item-field">
                     <div class="input">
                      <input format="Alphadash" max="100" nospace="true" autocomplete="new-password" name="bank_number" placeholder="T 3 ti 100 k t" :value="userData?.bank_number" disabled=""><span>0/100</span>
                    </div>
                  </div>
               </div>
               <div class="form__item"><button class="btn btn__big" disabled type="submit" >Cp nht thng tin ngn hng</button></div>
            </form>
         </div>
      </div>
   </div>
   <div class="partners">
      <div class="partners__content"><a class="wrap__image" href="/"><img src="/images/tworldsantosa-bottom.png" class=""></a></div>
   </div>
</div>
</template>

<script>
import axios from 'axios';
import SliderComponent from '../components/Slider.vue';
import CurrenttimeComponent from '../components/Currenttime.vue';
import store from '../store';
import router from '@/router';

export default {
  components: {
    SliderComponent,
    CurrenttimeComponent
  },
  computed: {
		userData() {
			return store.getters.get_UserData;
		},
	},
  data() {
    return {
        banks: [ 
            { value: 'VietinBank - Ngn hng Thng mi c phn Cng Thng Vit Nam' },
            { value: 'Techcombank - Ngn hng Thng mi c phn K Thng Vit Nam' },
            { value: 'Vietcombank - Ngn hng thng mi c phn Ngoi thng Vit Nam' },
            { value: 'VPBank - Ngn hng Thng mi c phn Vit Nam Thnh Vng' },
            { value: 'BIDV - Ngn hng Thng mi c phn u t v Pht trin Vit Nam' },
            { value: 'MB BANK - Ngn hng Thng mi C phn Qun i' },
            { value: 'ACB - Ngn hng thng mi c phn  Chu' },
            { value: 'VIB - Ngn hng TMCP Quc t Vit Nam' },
            { value: 'TPBank - Ngn hng Thng mi C phn Tin Phong' },
            { value: 'Agribank - Ngn hng Nng nghip v Pht trin Nng thn Vit Nam' },
            { value: 'ABBANK - Ngn hng Thng mi C phn An Bnh' },
            { value: 'Bo Vit (Baoviet bank)' },
            { value: 'Bn Vit  -  Viet Capital Bank' },
            { value: 'Bc  - Bac A Bank' },
            { value: 'Bu in Lin Vit   Lienviet Post Bank - LPB' },
            { value: 'i Chng Vit Nam - PVcomBank' },
            { value: 'ng ' },
            { value: 'ng Nam ' },
            { value: 'Hng Hi' },
            { value: 'Kin Long' },
            { value: 'NAM A BANK' },
            { value: 'Phng ng ' },
            { value: ' Quc dn' }, 
            { value: 'Si Gn' }, 
            { value: ' Si Gn Cng Thng' }, 
            { value: ' Si Gn  H Ni' }, 
            { value: 'Si Gn Thng Tn' },
            { value: 'Vit ' },
            { value: ' Vit Nam Thng Tn' }, 
            { value: 'Xng du Petrolimex' },
            { value: 'Xut Nhp Khu' },
            { value: 'HDBank' },
            { value: 'GP Bank' },
            { value: 'i Dng' },
            { value: 'Ngn hng TNHH MTV Xy dng' },
            { value: 'Hong Leong Vit Nam' },
            { value: 'HSBC Vit Nam' },
            { value: 'Shinhan Vit Nam' },
            { value: 'Standard Chartered Vit Nam' },
            { value: 'Public Bank Vit Nam' },
            { value: 'CIMB Vit Nam' },
            { value: 'Ngn hng TNHH MTV Woori Vit Nam' },
            { value: 'Ngn hng TNHH MTV UOB Vit Nam' }
        ],
        bank_name: null,
        bank_user: null,
        bank_number: null,
        cccd_mt: null,
        cccd_ms: null,
    };
  },
  methods: {
    async addnewbank() {
      console.log('addnewbank')
      if (this.bank_name == null || this.bank_user == null || this.bank_number == null || this.cccd_mt == null || this.cccd_ms == null ) {
        return;
      }
      try {
        const token = localStorage.getItem('token');
        const response = await axios.post(`${process.env.VUE_APP_API_URL}/addnewbank`, 
        {
            bank_name: this.bank_name,
            bank_user: this.bank_user,
            bank_number: this.bank_number,
            cccd_mt: this.cccd_mt,
            cccd_ms: this.cccd_ms,
        }, 
        {
          headers: {
            Authorization: `Bearer ${token}`,
            'Content-Type': 'multipart/form-data'
          }
        });
        const data = response.data
        console.log(data)
        if (data.status == 1) {
          this.$toast.success(data.msg);
          setTimeout(() => {
            router.push('/tai-khoan');
          }, 1000);
        } else {
          this.$toast.error(data.msg);
        }
      } catch (err) {
        console.log(err)
      }
    },
    handleFileUpload(event) {
      const file = event.target.files[0];
      this.cccd_mt = file;
    },
    handleFileUploadms(event) {
      const file = event.target.files[0];
      this.cccd_ms = file;
    },
    async withdrawmoney() {
      if( this.balance > this.userData.balance || this.balance == 0 ){
          Swal.fire({
            title: 'S d ca qu khch khng  !',
            showConfirmButton: false,
            showCancelButton: true,
            cancelButtonText: 'ng',
          });
      } else{
        try {
          const token = localStorage.getItem('token');
          const response = await axios.post(`${process.env.VUE_APP_API_URL}/withdraw`, 
          {
            balance: this.balance
          }, 
          {
            headers: {
              Authorization: `Bearer ${token}`
            }
          });
          const data = response.data
          console.log(data)
          if (data.status == 1) {
            this.$toast.success(data.msg);
          } else {
            this.$toast.error(data.msg);
          }
        } catch (err) {
          console.log(err)
        }
      }
    },
    togglePasswordVisibility() {
      this.passwordVisible = !this.passwordVisible;
    },
  },
}
</script>

<style scoped>
.form__item select {
    background: var(--light);
    border: 1px solid var(--block);
    color: var(--default);
    width: 100%;
    flex: 0 0 100%;
    padding: 10px;
    border-radius: 10px;
}
</style>